<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">确认订单</span>
      </div>
      <div class="content-wrapper">
        <div class="address-wrapper" v-show="hasAddress">
          <div class="icon-wrapper">
            <i></i>
          </div>
          <div class="address">
            <p class="name">
              <label>{{addressInfo.contacts}}</label>
              <span>{{addressInfo.contactNumber}}</span>
            </p>
            <p class="position">{{addressInfo.address}}</p>
          </div>
          <div class="forward-wrapper" @click="goToAddress">
            <i></i>
          </div>
        </div>
        <div class="address-wrapper" v-show="!hasAddress">
          <div class="no-address">
            <em>+</em><span>点击添加收货地址</span>
          </div>
          <div class="no-address-forward" @click="goToAddress">
            <i></i>
          </div>
        </div>
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in shoppingCartItems">
              <div class="left">
                <img :src="item.src" />
              </div>
              <div class="right">
                <p class="name">{{item.title}}</p>
                <div class="desc">
                  <p><label>规格：</label><span>{{item.desc}}</span></p>
                </div>
                <div class="calc">
                  <p class="num"><label>x</label><span>{{item.count}}</span></p>
                  <p class="price"><label>￥</label><span>{{item.price | formatMoney}}</span></p>
                </div>
              </div>
            </li>
          </ul>
        </scroll>
        <div class="settle-wrapper">
          <settle @submit="submit" :price="price" :total="total" confirmText="确认订单"></settle>
        </div>
      </div>
      <router-view></router-view>
      <alert ref="alert" :message="alertMsg"></alert>
    </div>
  </transition>
</template>


<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import Settle from 'base/settle/settle'
  import Alert from 'base/alert/alert'
  import {mapGetters,mapState,mapActions} from 'vuex'

  import {ERR_CODE} from 'api/config'
  import {getAddress,confirmShoppingCart,deleteShoppingCart} from 'api/purchase'

  export default {
    data () {
      return {
        alertMsg: ''
      }
    },
    computed: {
      ...mapGetters([
        'shoppingCartItems'
      ]),
      ...mapState([
        'addressInfo'
      ]),
      currentRoute(){
        return this.$router.currentRoute.path
      },
      hasAddress() {
        if(this.addressInfo.contacts || this.addressInfo.contactNumber || this.addressInfo.address ){
          return true
        }
        return false
      },
      isRightAddress() {
        if(this.addressInfo.contacts && this.addressInfo.contactNumber && this.addressInfo.address ){
          return true
        }
        return false
      },
      total() {
        let tempTotal = 0
        for(let key in this.shoppingCartItems){
          tempTotal += this.shoppingCartItems[key].count
        }
        return tempTotal
      },
      price() {
        let tempPrice = 0
        for(let key in this.shoppingCartItems){
          let value = this.shoppingCartItems[key]
          tempPrice += value.price * value.count
        }
        return tempPrice
      }
    },
    created () {
      this._getAddress()
    },
    methods: {
      back() {
        this.$router.back()
      },
      showAlert() {
        this.$refs.alert.show()
      },
      _getAddress(){
        getAddress().then((res) => {
          if(res.errCode === ERR_CODE){
            if(res.data && res.data.length){
              this.setAddressInfo(res.data[0])
            } else {
              this.setAddressInfo()
            }
          } else {
            this.setAddressInfo()
          }
        })
      },
      goToAddress() {
        this.$router.push({
          path: `${this.currentRoute}/address`
        })
      },
      _check() {
        if(!this.isRightAddress){
          this.alertMsg = '请先完善收货地址'
          this.showAlert()
          return false
        }
        return true
      },
      // 跳转到提交订单模块
      submit(){
        if(!this._check()){
          return
        }
        let entries = []
        let ids = []
        for(let key in this.shoppingCartItems){
          let shoppingCartItem = this.shoppingCartItems[key]
          if(shoppingCartItem.count > 0){
            ids.push(shoppingCartItem.id)
            entries.push({
              productItemId: shoppingCartItem.productItemId,
              count: shoppingCartItem.count
            })
          }
        }
        confirmShoppingCart({
          entries,
          remark: '',
          merchantShippingAddressId: this.addressInfo.id
        }).then((res) => {
          if(res.errCode === ERR_CODE){
            ids.forEach((id) => {
              deleteShoppingCart(id)
            })
            this.setShoppingCart()
            this.$router.push({
              path: '/purchase/myorder'
            })
          }
        })
      },
      ...mapActions([
        'setAddressInfo',
        'setShoppingCart'
      ])
    },
    components: {
      Settle,
      Scroll,
      Alert
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      flex-direction column
      .address-wrapper
        width 100%
        min-height 84px
        margin-top 10px
        background #fff
        display flex
        .icon-wrapper
          margin-left 15px
          margin-bottom 18px
          display flex
          align-items flex-end
          i
            display inline-block
            background url("~common/image/address.png") no-repeat
            background-size contain
            width 13px
            height 17px
        .address
          color #333
          display flex
          flex 1
          flex-direction column
          margin-left 14px
          margin-right 14px
          max-width calc(100% - 80px)
          overflow hidden
          .name
            display flex
            align-items center
            margin-top 14px
            height 24px
            font-size $font-size-medium-x
            label,span
              font-weight bold
              margin-right 16px
          .position
            display flex
            align-items center
            height 24px
            font-weight bold
            font-size $font-size-medium
            margin-top 8px
        .forward-wrapper
          margin-right 15px
          margin-bottom 22px
          display flex
          align-items flex-end
          i
            display inline-block
            position relative
            background url("~common/image/forward.png") no-repeat
            background-size contain
            width 6px
            height 10px
            &:before
              position absolute
              left -10px
              top -10px
              right -10px
              bottom -10px
              content ''
        .no-address
          display flex
          align-items center
          justify-content center
          width 100%
          color #888
          font-size $font-size-medium-x
          em
            margin-right 4px
        .no-address-forward
          margin-right 15px
          display flex
          align-items center
          i
            display inline-block
            position relative
            background url("~common/image/forward.png") no-repeat
            background-size contain
            width 6px
            height 10px
            &:before
              position absolute
              left -10px
              top -10px
              right -10px
              bottom -10px
              content ''
      .content
        width 100%
        overflow hidden
        margin-top 10px
        .list
          background #fff
          width 100%
          .item
            height 110px
            display flex
            .left
              width 94px
              height 100%
              display flex
              align-items center
              justify-content center
              img
                display block
                height 70px
                width 70px
            .right
              display flex
              flex 1
              flex-direction column
              margin-right 15px
              border-bottom 1px solid #f3f3f3
              .name
                font-size $font-size-medium-x
                color #4c4c4c
                margin-top 10px
                height 32px
                font-weight bold
              .desc
                display flex
                height 36px
                flex-shrink 0
                align-items center
                color #4c4c4c
                font-size $font-size-small-x
              .calc
                display flex
                height 24px
                justify-content space-between
                align-items center
                font-size $font-size-small-x
                .num
                  color #4c4c4c
                  font-weight bold
                .price
                  color #eb4747
                  label
                    font-size $font-size-small-x
                  span
                    font-weight bold
                    font-size $font-size-medium-x
      .settle-wrapper
        position fixed
        left 0
        right 0
        bottom 0
        height 45px

</style>




